html, body, * {
    margin: 0;
    padding: 0;
}

.App {
    text-align: center;
    width: 100%;
}

.header {
    width: 100%;
}

.logo {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    background: skyblue;
    letter-spacing: 2px;
    height: 72px;
}

.title {
    font-size: 48px;
    margin: 24px;
}

.battle {
    display: flex;
    justify-content: center;
}

.battle-content {
    max-width: 768px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.battle-loading {
    padding: 96px;
}

.service {
    display: flex;
    flex-direction: column;
    padding: 48px;
    width: 320px;
}

.service.small {
    width: 80px;
    padding: 10px;
}

.service.small .service-image img {
    width: 80px;
}

.service.small > .service-text {
    padding: 0;
    font-size: 12px;
}

.service-image {
    width: 100%;
}

.service-image img {
    width: 192px;
    /*max-width: 400px;*/
}

.service-text {
    font-size: 24px;
    padding: 32px 12px;
    font-weight: bold;
}

.btn {
    border: 0;
    background: none;
}

.btn-primary {
    width: 100%;
    border: 4px inset red;
    font-size: 18px;
    transition: .3s all;
    height: 42px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-primary div {
    transition: .3s all;
}

.btn-primary:hover > div {
    transform: scale(2);
}

.learn-more {
    display: inline-block;
    font-size: 16px;
    padding: 12px;
    color: black;
    text-decoration: none;
}

.learn-more:hover {
    text-decoration: underline;
}

.service.small .learn-more {
    padding: 0;
    font-size: 12px;
}

.leaderboard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

.leaderboard-content {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
}

@media screen and (max-width: 786px) {
    .service {
        width: 48%;
        padding: 0;
        padding: 10px;
    }

    .service img {
        width: 96px;
    }

    .service-text {
        padding: 10px 0px;
        font-size: 18px;
    }

    .title {
        font-size: 36px;
    }
}